import React from 'react';
import { graphql, Link } from 'gatsby';
import Layout from '../components/layout';
import Img from 'gatsby-image';
import {kebabCase} from "lodash";
import Fade from 'react-reveal/Fade';
import { ThemeToggler } from 'gatsby-plugin-dark-mode';
import { CustomInput} from 'reactstrap';

class BlogPage extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            theme : 'dark',
            checked : false
        };

        this.handleTheme = this.handleTheme.bind(this);
        this.handleChange = this.handleChange.bind(this);
    }

    handleTheme (events){

        console.log(events);
    };

    handleChange(checked) {
        this.setState({ checked });
    }

    render() {

        const posts = this.props.data.allMarkdownRemark.edges;

        return (

            <ThemeToggler>
                {({ theme, toggleTheme }) => (
            <Layout >
                <h1 style={{paddingTop: "50px", fontFamily: "Blippo"}}>Aman Dave
                    <div style={{ float: "right", fontSize: "20px"}}>
                        <CustomInput
                            onChange={e => toggleTheme(e.target.checked ? 'dark' : 'light')}
                            checked={theme === 'dark'}
                            type="switch" id="exampleCustomSwitch" name="customSwitch" label="Dark Mode" />
                    </div>
                </h1>

                <br/>
                <Fade right>
                    <div
                        style={{
                            backgroundColor: 'var(--bg)',
                            color: 'var(--textNormal)',
                            transition: 'color 0.2s ease-out, background 0.2s ease-out',
                        }}
                        className="post-list">
                        {posts.map(post => (
                            <div key={post.node.id} className="post-list__item" style={{
                                border: "1px solid #393e46", marginBottom: "50px"}}>
                                <div className="post-list__thumbnail">
                                    <Link to={post.node.fields.slug}>
                                        <Img
                                            fixed={post.node.frontmatter.thumbnail.childImageSharp.fixed}
                                        />
                                    </Link>
                                </div>
                                <div className="post-list__content">
                                    <Link to={post.node.fields.slug}>
                                        <h2>{post.node.frontmatter.title}</h2>
                                    </Link>
                                    {post.node.frontmatter.tags ? (
                                        <div className="tags-container">
                                            <ul className="taglist">
                                                {post.node.frontmatter.tags.map(tag => (
                                                    <li key={tag + `tag`}>
                                                        <Link to={`/tags/${kebabCase(tag)}/`}>{tag}</Link>

                                                    </li>
                                                ))}
                                            </ul>
                                        </div>
                                    ) : null}

                                    <p>{post.node.frontmatter.date}</p>
                                    <div className="post-list__excerpt">{post.node.excerpt}</div>

                                </div>
                            </div>
                        ))}
                    </div>
                </Fade>
            </Layout>
                )}
            </ThemeToggler>
        );
    };
}
export default BlogPage;

// Get all markdown data, in descending order by date, and grab the id, excerpt, slug, date, and title
//blog.js
export const pageQuery = graphql`
  query {
    allMarkdownRemark(sort: { order: DESC, fields: [frontmatter___date] }) {
      edges {
        node {
          id
          excerpt(pruneLength: 250)
          fields {
            slug
          }
          frontmatter {
            date(formatString: "MMMM DD, YYYY")
            title
            tags
            thumbnail {
              childImageSharp {
                fixed(width: 200, height: 200) {
                  ...GatsbyImageSharpFixed
                }
              }
            }
          }
        }
      }
    }
  }
`;
